/* ðŸ”¥ Tema oscuro inspirado en Netflix */
body {
    background-color: #141414;
    color: white;
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
  }
  
  /* âœ… Navbar estilo Netflix */
  nav {
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: #181818;
    padding: 15px;
  }
  
  nav a {
    color: #e50914;
    text-decoration: none;
    font-size: 18px;
    font-weight: bold;
    transition: 0.3s;
  }
  
  nav a:hover {
    color: white;
  }
  
  /* ðŸŽ¬ Estilo para tarjetas de pelÃ­culas */
  .movie-card {
    width: 200px;
    border-radius: 10px;
    overflow: hidden;
    transition: 0.3s;
  }
  
  .movie-card img {
    width: 100%;
    border-radius: 10px;
  }
  
  .movie-card:hover {
    transform: scale(1.1);
    cursor: pointer;
  }
  
  /* ðŸ”¥ Contenedor del formulario */
.form-container {
    max-width: 400px;
    margin: auto;
    padding: 20px;
    background-color: #181818;
    border-radius: 10px;
    box-shadow: 0px 0px 15px rgba(255, 255, 255, 0.1);
    text-align: center;
  }
  
  /* âœ… Campos del formulario */
  input {
    width: 100%;
    padding: 10px;
    margin: 10px 0;
    border: none;
    border-radius: 5px;
    background-color: #333;
    color: white;
  }
  
  /* ðŸ”¹ BotÃ³n de registro/login */
  button {
    width: 100%;
    padding: 10px;
    background-color: #e50914;
    color: white;
    font-weight: bold;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: 0.3s;
  }
  
  button:hover {
    background-color: #ff2020;
  }
  